.about-me {
  background-image: radial-gradient(
    $color-sunglow,    
    $color-yellow-sea,  
  );
  padding: 1rem;
 }

.about-me__header {
  color: $color-white;
  @extend %header;
}

.about-me__avatar-wrapper {
  margin: 3rem auto;
  border: 0.3rem solid $color-fiord;
  border-radius: 50%;
  position: relative;
  background-image: url(asset/image/avatar.png);
  width: 50%;
  padding: 25%;
  background-size: cover;

  &::after {
    content:'';
    position: absolute;
    border: 0.5rem solid rgba($color: $color-fiord, $alpha: 0.3);
    top: -1rem;
    bottom: -1rem;
    left: -1rem;
    right: -1rem;
    border-radius: 50%;
  }

}

.about-me__full-name {
  color: $color-white;
  font-size: 3rem;
  text-align: center;
}

.about-me__title {
  font-size: 2.25rem;
  color: $color-white;
  text-align: center;
  padding: 0.3rem 1rem 1.5rem;
}

.about-me__motto,
.about-me__content {
  font-size: 1.5rem;
  text-align: justify;
  text-indent: 4rem;
  color: $color-fiord;
  padding: 1rem;
}

.about-me__link-list {
  text-align: center;
}

.about-me__link {
  display: flex;
  width: 80%;
  font-size: 3rem;
  text-decoration: none;
  border: 0.3rem solid $color-white;
  border-radius: 1.3rem;
  background-color: $color-fiord;
  margin: 0.4rem auto;
  justify-content: space-around;
  align-items: center;
  color: $color-white;
  padding: 0.5rem;

  &:hover {
    color: $color-sunglow;
  }
}

.about-me__link-description {
  font-size: 1.5rem;
  line-height: 1.5;
  flex-grow: 1;  
}